//the imports
import "ol/ol.css";
import "ol-layerswitcher/dist/ol-layerswitcher.css";
import XYZ from "ol/source/XYZ.js";
import Map from "ol/Map.js";
import OSM from "ol/source/OSM.js";
import TileLayer from "ol/layer/Tile.js";
import TileWMS from "ol/source/TileWMS.js";
import View from "ol/View.js";
import LayerSwitcher from "ol-layerswitcher";
import { Image as ImageLayer } from "ol/layer.js";
import ImageWMS from "ol/source/ImageWMS.js";

//the background map layers

const positronBackgroundLayer = new TileLayer({
  source: new XYZ({
    url: "https://cartodb-basemaps-a.global.ssl.fastly.net/light_all/{z}/{x}/{y}.png",
    maxZoom: 20,
  }),
});

const osmBackgroundLayers = new TileLayer({
  source: new OSM(),
  title: "osm",
});

//the vector layers

const batimentLayer = new TileLayer({
  source: new TileWMS({
    url: "http://geoserver-mobilite-app.herokuapp.com/mobilite/wms",
    params: { LAYERS: "mobilite:batiment_wgs84", TILED: true },
    serverType: "geoserver",
    // Countries have transparency, so do not fade tiles:
    transition: 0,
  }),
  title: "batiments",
  visible: false,
});
const routeLayer = new TileLayer({
  source: new TileWMS({
    url: "http://geoserver-mobilite-app.herokuapp.com/mobilite/wms",
    params: { LAYERS: "	mobilite:route_wgs84", TILED: true },
    serverType: "geoserver",
    // Countries have transparency, so do not fade tiles:
    transition: 0,
  }),
  title: "tronçons",
  visible: false,
});
const communeLayer = new TileLayer({
  source: new TileWMS({
    url: "http://geoserver-mobilite-app.herokuapp.com/mobilite/wms",
    params: { LAYERS: "	mobilite:commune_wgs84", TILED: true },
    serverType: "geoserver",
    // Countries have transparency, so do not fade tiles:
    transition: 0,
  }),
});
const treeLayer = new TileLayer({
  source: new TileWMS({
    url: "http://geoserver-mobilite-app.herokuapp.com/mobilite/wms",
    params: { LAYERS: "	mobilite:tree_grid_wgs84", TILED: true },
    serverType: "geoserver",
    // Countries have transparency, so do not fade tiles:
    transition: 0,
  }),
  title: "densité de la végetation",
  visible: false,
});
const wmsSource = new ImageWMS({
  url: "http://geoserver-mobilite-app.herokuapp.com/mobilite/wms",
  params: { LAYERS: "mobilite:mesurement_grid_wgs84" },
  ratio: 1,
  serverType: "geoserver",
});

const airMesurementLayer = new ImageLayer({
  source: wmsSource,
});

const vegetationZoneLayer = new TileLayer({
  source: new TileWMS({
    url: "http://geoserver-mobilite-app.herokuapp.com/mobilite/wms",
    params: { LAYERS: "	mobilite:zone_vege_wgs84", TILED: true },
    serverType: "geoserver",
    // Countries have transparency, so do not fade tiles:
    transition: 0,
  }),
  title: "zones végétalisées",
  visible: false,
});
//the legend
const updateLegend = function (resolution) {
  const graphicUrl = wmsSource.getLegendUrl(resolution);
  const img = document.getElementById("legend");
  img.src = graphicUrl;
};

//the map
const map = new Map({
  layers: [
    osmBackgroundLayers,
    positronBackgroundLayer,
    treeLayer,
    routeLayer,
    batimentLayer,
    communeLayer,
    airMesurementLayer,
    vegetationZoneLayer,
  ],
  target: "map",
  view: new View({
    center: [638000, 5650900],
    zoom: 15,
  }),
});
// Initial legend
const resolution = map.getView().getResolution();
updateLegend(resolution);

//layer switcher
const layerSwitcher = new LayerSwitcher({
  reverse: true,
  groupSelectStyle: "group",
});
map.addControl(layerSwitcher);
