//the sript that allow recuperation of the data from geoserver as response, and to map it into a py_graphe (data of parc)
fetch(
  "http://geoserver-mobilite-app.herokuapp.com/mobilite/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=mobilite:trajet_1,mobilite:trajet_2,mobilite:trajet_3&outputFormat=application%2Fjson"
) // in the URL, find as "get" all the data basicly spliited on geoserver
  .then((response) => response.json())
  .then((data) => {
    //getting the data as response, and with all layers in ones
    var separatedData = data.features.reduce((acc, feature) => {
      // fonction that allow spliting the data on the call :
      var layerName = feature.id.split(".")[0];
      acc[layerName] = acc[layerName] || [];
      acc[layerName].push(feature);
      return acc;
    }, {});
    console.log(separatedData);
    //the seconde trace as "trajet2"
    var trace2 = {
      x: separatedData.trajet_2.map(
        (feature) => feature.properties.id_enregistrement
      ),
      y: separatedData.trajet_2.map(
        (feature) => feature.properties.moyenne_mesure_air
      ),
      type: "scatter",
      name: "matin",
      marker: { color: "bdbdbd" },
    };
    // the first trace "trajet1"
    var trace1 = {
      x: separatedData.trajet_1.map(
        (feature) => feature.properties.id_enregistrement
      ),
      y: separatedData.trajet_1.map(
        (feature) => feature.properties.moyenne_mesure_air
      ),
      type: "scatter",
      name: "aprés-midi",
      marker: { color: "434343" },
    };

    var data = [trace1, trace2];
    //third trace as "trajet3"
    var trace3 = {
      x: separatedData.trajet_3.map(
        (feature) => feature.properties.id_enregistrement
      ),
      y: separatedData.trajet_3.map(
        (feature) => feature.properties.moyenne_mesure_air
      ),
      type: "scatter",
      name: "soir",
      marker: { color: "1f77b4" },
    };

    //ploting the data
    var data = [trace1, trace2, trace3];
    var layout = {
      title: "Qualité de l'air pour les trajets au parc Jean-Verlack",
    };
    Plotly.newPlot("myDiv", data, layout, {
      displayModeBar: false,
      responsive: true,
    });
  });

