fetch(
  "http://geoserver-mobilite-app.herokuapp.com/mobilite/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=mobilite:trajet_1,mobilite:trajet_2,mobilite:trajet_3,mobilite:trajet_4,mobilite:trajet_5,mobilite:trajet_6&outputFormat=application%2Fjson"
) // in the URL, find as "get" all the data basicly spliited on geoserver
  .then((response) => response.json())
  .then((data) => {
    let separatedDataLieux = data.features.reduce((acc, feature) => {
      var lieux = feature.properties.lieux;
      acc[lieux] = acc[lieux] || [];
      acc[lieux].push(feature);
      return acc;
    }, {});
    console.log(separatedDataLieux);
    var trace7 = {
      x: separatedDataLieux.hyper_centre.map(
        (feature) => feature.properties.moyenne_mesure_air
      ),
      type: "box",
      name: "hyper-centre",
    };

    var trace6 = {
      x: separatedDataLieux.parc_jean_verlhac.map(
        (feature) => feature.properties.moyenne_mesure_air
      ),
      type: "box",
      name: "parc",
    };
    var layout = {
      title:
        "Box plot des valeurs de qualité de l'air au par et à l'hyper-centre",
    };
    var data = [trace7, trace6];

    Plotly.newPlot("myDiv2", data, layout, { displayModeBar: false });
  });
